import React, {useEffect, useState} from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro"; //eslint-disable-line



import Header, { LogoLink, NavLinks, NavLink as NavLinkBase } from "../headers/light.js";

const FILE_URL = 'http://localhost:3000/hello.docx'


const StyledHeader = styled(Header)`
  ${tw`justify-between`}
  ${LogoLink} {
    ${tw`mr-8 pb-0`}
  }
`;

const NavLink = tw(NavLinkBase)`
  sm:text-sm sm:mx-6
`;

const Container = tw.div`relative -mx-8 -mt-8`;
const TwoColumn = tw.div`flex flex-col lg:flex-row bg-gray-100`;
const LeftColumn = tw.div`ml-8 mr-8 xl:pl-10 py-8`;
const RightColumn = styled.div`
  background-image: url("https://images.unsplash.com/photo-1551918120-9739cb430c6d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&width=1440&height=1024&q=75");
  ${tw`bg-green-500 bg-cover bg-center xl:ml-24 h-96 lg:h-auto lg:w-1/2 lg:flex-1`}
`;
const Content = tw.div`mt-24 lg:mt-24 lg:mb-24 flex flex-col sm:items-center lg:items-stretch`;
const Heading = tw.h1`text-3xl sm:text-5xl md:text-6xl lg:text-5xl font-black leading-none`;
const Paragraph = tw.p`max-w-md my-8 lg:my-5 lg:my-8 sm:text-lg lg:text-base xl:text-lg leading-loose`;


const Actions = styled.div`
  ${tw`mb-8 lg:mb-0`}
  .action {
    ${tw`text-center inline-block w-full sm:w-48 py-4 font-semibold tracking-wide rounded hocus:outline-none focus:shadow-outline transition duration-300`}
  }
  .primaryAction {
    ${tw`bg-primary-400 text-gray-100 hover:bg-primary-500`}
  }
  .secondaryAction {
    ${tw`mt-4 sm:mt-0 sm:ml-4 bg-gray-300 text-gray-700 hover:bg-gray-400 hover:text-gray-800`}
  }
`;




export default ({
  navLinks = [
    <NavLinks key={1}>
      <NavLink href="#">About</NavLink>
      <NavLink href="#">Blog</NavLink>
      <NavLink href="#">Pricing</NavLink>
      <NavLink href="#">Login</NavLink>
    </NavLinks>
  ],
  heading = (
    <>
      Find Property Details
      <wbr />
      <br />
      <span tw="text-primary-500">Test Website</span>
    </>
  ),
  description = "Welcome to the Test Page",
  primaryActionUrl = "#",
  primaryActionText = "Sign Up",
  secondaryActionUrl = "#",
  secondaryActionText = "Download File"
}) => {

  const [info, setInfo] = useState({});

  useEffect(() => {
    fetch("http://127.0.0.1:8000/")
        .then(response => response.json())
        .then(data => {
          console.log("API Response:", data); // Check the API response in the console
          setInfo(data);
        })
        .catch(error => {
          console.error("API Error:", error); // Check for any errors in the console
        });
  }, []);


  const [postResponse, setPostResponse] = useState("");

  useEffect(() => {
    // POST request using fetch inside useEffect React hook
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ address: 'BA14 9DH' })
    };
    fetch('http://127.0.0.1:8000/items', requestOptions)
        .then(response => response.json())
        .then(data => {
          console.log("Post Request Response:", data)
          setPostResponse(data)
        });

// empty dependency array means this effect will only run once (like componentDidMount in classes)
  }, []);




  const downloadFileAtURL=(url, fileName)=>{
    const aTag = document.createElement('a');
    aTag.href=url;
    aTag.setAttribute('download',fileName);
    document.body.appendChild(aTag);
    aTag.click();
    aTag.remove();
  }




  return (
    <Container>
      <TwoColumn>
        <LeftColumn>
          <StyledHeader links={navLinks} collapseBreakpointClass="sm" />
          <Content>
            <Heading>{heading}</Heading>
            <Paragraph>{info.Hello}</Paragraph>
            <Paragraph>Post ID: {postResponse.address}</Paragraph>
            <Actions>
              <a href={primaryActionUrl} className="action primaryAction">
                {primaryActionText}
              </a>
              <button className="action secondaryAction"
                      onClick={() => {
                      downloadFileAtURL(FILE_URL, "example_1")}}>
                {secondaryActionText}
              </button>
              <button className="action primaryAction"
                      onClick={() => {
                      downloadFileAtURL("http://127.0.0.1:8000/file", "API_Example")}}>
                Download from API
              </button>
            </Actions>
          </Content>
        </LeftColumn>
        <RightColumn></RightColumn>
      </TwoColumn>
    </Container>
  );
};
